<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" />
    <van-tabs v-model="activeName">
      <van-tab name="all" title="全部"></van-tab>
      <van-tab name="payment" title="待支付"></van-tab>
      <van-tab name="delivery" title="待发货"></van-tab>
      <van-tab name="received" title="待收货"></van-tab>
      <van-tab name="comment" title="待评价"></van-tab>
    </van-tabs>

    <OrderListItem v-for="item in list" :key="item.order_id" :item="item"></OrderListItem>
  </div>
</template>

<script>
import { getOrderListApi } from '@/api/order'
import OrderListItem from '@/components/OrderListItem.vue'
export default {
  name: 'OrderPage',
  components: {
    OrderListItem
  },
  data () {
    return {
      activeName: this.$route.query.dataType || 'all',
      list: [],
      goodsList: [],
      page: 1
    }
  },
  created () {
    // this.getOrderList() // 渲染
  },
  methods: {
    // 封装获取商品订单
    async getOrderList () {
      const res = await getOrderListApi(this.activeName, this.page)
      const { data: { list } } = res
      list.data.forEach((item) => {
        item.total_num = 0
        item.goods.forEach(goods => {
          item.total_num += goods.total_num
        })
      })
      this.list = list.data
      // console.log(res)
    }
  },
  // 利用监听activeName的值去执行变化时所传递的不同值
  watch: {
    activeName: {
      deep: true,
      immediate: true, // 立马执行一次
      handler () {
        this.getOrderList()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
}

.van-tabs {
  position: sticky;
  top: 0;
}
</style>
